import { Component, OnInit } from '@angular/core';
import {validateNric} from '../generator/generator-logic';

@Component({
  standalone: false,
  selector: 'app-validator',
  templateUrl: './validator.component.html',
  styleUrls: ['./validator.component.scss']
})
export class ValidatorComponent implements OnInit {
  enteredNric = '';
  valid: boolean;

  constructor() { }

  ngOnInit(): void {

  }

  nricChanged($event: Event) {
    this.enteredNric = ($event.target as HTMLInputElement).value
    this.valid = validateNric(this.enteredNric)
  }
}
